import html2canvas from 'html2canvas';
import { useState } from 'react';
import './App.css';

function App() {

  const [primerTexto, setPrimerTexto] = useState('');
  const [segundoTexto, setSegundoTexto] = useState('');
  const [meme, setMeme] = useState('fire');

  const cambioPrimerTexto = function (evento) {
    setPrimerTexto(evento.target.value);
  }

  const cambioSegundoTexto = function (evento) {
    setSegundoTexto(evento.target.value);
  }

  const cambioDeMeme = function (evento) {
    setMeme(evento.target.value);
  }

  const exportarMeme = function (evento) {
    html2canvas(document.querySelector(".meme")).then(canvas => {
      var img = canvas.toDataURL("img/png");
      var link = document.createElement('a');
      link.download = 'meme.png';
      link.href = img;
      link.click();
    });
  }

  return (
    <div className="App">

      <select onChange={cambioDeMeme}>
        <option value="fire" selected>Casa en llamas</option>
        <option value="futurama">Futurama</option>
        <option value="history">Aliens</option>
      </select><br />

      <input onChange={cambioPrimerTexto} type="text" placeholder="Primer texto"></input><br />

      <input onChange={cambioSegundoTexto} type="text" placeholder="Segundo texto"></input><br />

      <button onClick={exportarMeme}>Exportar</button>

      <div className="meme">
        <span>{primerTexto}</span><br />
        <span className="segundoTexto">{segundoTexto}</span><br />
        <img src={"/images/" + meme + ".jpg"} alt="Imagen elegida como meme"></img>
      </div>
    </div>
  );
}

export default App;
